#container {
  width: 100%;
  margin: 0px auto;
}
#songTitle {
  position: absolute;
  display: inline-block;
  padding: 0px 5px;
}
#canvas {
  height: 300px;
  padding-top: 50px;
}
#svgToRight {
  background-color: rgba(255.255.255.0.5);
  position: absolute;
  height: 500px;
  width: 50px;
  right: 0px;
  top: 0px;
}

#songDescription {
  position: fixed;
  width: 100%;
  bottom: 0px;
  z-index: 2;
  height: 300px;
}

h5 {
  -webkit-margin-before: 0.5em;
  -webkit-margin-after: 0.5em;
}

.footer {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: lighter;
  font-size: 10px,
}


